import { useState, useEffect } from "react";
import { Card } from "../components/ui/Card";
import { Button } from "../components/ui/Button";
import { api } from "../services/api";

export function RestrictionPage() {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        setLoading(true);
        const response = await api.get("/users");
        setUsers(response.data);
      } catch (err) {
        setError("Failed to fetch users");
      } finally {
        setLoading(false);
      }
    };
    fetchUsers();
  }, []);

  const getStatusBadge = (user) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full";
    if (!user.isVerified) {
      return <span className={`${baseClasses} bg-red-100 text-red-800 border border-red-300`}>Suspended</span>;
    } else if (user.role === 'restricted') {
      return <span className={`${baseClasses} bg-orange-100 text-orange-800 border border-orange-300`}>Restricted</span>;
    } else {
      return <span className={`${baseClasses} bg-green-100 text-green-800 border border-green-300`}>Active</span>;
    }
  };

  const getRoleBadge = (role) => {
    const baseClasses = "px-2 py-1 text-xs font-medium rounded-full";
    if (role === 'restricted') {
      return <span className={`${baseClasses} bg-orange-100 text-orange-800 border border-orange-300`}>restricted</span>;
    } else {
      return <span className={`${baseClasses} bg-blue-100 text-blue-800 border border-blue-300`}>active</span>;
    }
  };

  // Add plan mapping
  const planNumberToName = { 1: 'basic', 2: 'premium', 3: 'premiumPlus', '1': 'basic', '2': 'premium', '3': 'premiumPlus' };

  const handleRestrict = async (userId) => {
    if (window.confirm("Are you sure you want to restrict this user?")) {
      try {
        await api.patch(`/users/${userId}/restrict`);
        // Refresh users list
        const response = await api.get("/users");
        setUsers(response.data);
        alert("User restricted successfully!");
      } catch (err) {
        alert("Failed to restrict user");
        console.error("Error restricting user:", err);
      }
    }
  };

  const handleSuspend = async (userId) => {
    if (window.confirm("Are you sure you want to suspend this user?")) {
      try {
        await api.patch(`/users/${userId}/suspend`);
        // Refresh users list
        const response = await api.get("/users");
        setUsers(response.data);
        alert("User suspended successfully!");
      } catch (err) {
        alert("Failed to suspend user");
        console.error("Error suspending user:", err);
      }
    }
  };

  const handleActivate = async (userId) => {
    if (window.confirm("Are you sure you want to activate this user?")) {
      try {
        await api.patch(`/users/${userId}/activate`);
        // Refresh users list
        const response = await api.get("/users");
        setUsers(response.data);
        alert("User activated successfully!");
      } catch (err) {
        alert("Failed to activate user");
        console.error("Error activating user:", err);
      }
    }
  };

  if (loading) return (
    <div className="p-6">
      <Card className="p-6 text-center text-gray-400">Loading users...</Card>
    </div>
  );
  
  if (error) return (
    <div className="p-6">
      <Card className="p-6 text-center text-red-400">Error: {error}</Card>
    </div>
  );

  return (
    <div className="p-6 max-w-7xl mx-auto">
      <Card className="p-6">
        <div className="flex items-center justify-between mb-4">
          <h1 className="text-2xl font-bold text-white">User Management</h1>
        </div>
        <div className="overflow-x-auto rounded-md border border-gray-700">
          <table className="min-w-full divide-y divide-gray-600 text-sm text-left text-white">
            <thead className="bg-[#1f2937]">
              <tr>
                <th className="px-4 py-3 font-medium uppercase tracking-wide">USER</th>
                <th className="px-4 py-3 font-medium uppercase tracking-wide">PLAN</th>
                <th className="px-4 py-3 font-medium uppercase tracking-wide">ROLE</th>
                <th className="px-4 py-3 font-medium uppercase tracking-wide">STATUS</th>
                <th className="px-4 py-3 font-medium uppercase tracking-wide">ACTIONS</th>
              </tr>
            </thead>
            <tbody className="divide-y divide-gray-800 bg-[#0f172a]">
              {users.map(user => (
                <tr key={user._id} className="hover:bg-[#1e293b] transition">
                  <td className="px-4 py-2 font-semibold">{user.username}</td>
                  <td className="px-4 py-2">{planNumberToName[user.plan] || 'Free'}</td>
                  <td className="px-4 py-2">{getRoleBadge(user.role)}</td>
                  <td className="px-4 py-2">{getStatusBadge(user)}</td>
                  <td className="px-4 py-2">
                    <div className="flex gap-2">
          <Button 
                        variant="outline" 
            size="sm" 
                        className="text-xs px-3 py-1 bg-orange-600 hover:bg-orange-700 text-white border-orange-600"
                        onClick={() => handleRestrict(user._id)}
          >
            Restrict
          </Button>
          <Button 
            variant="outline" 
            size="sm" 
                        className="text-xs px-3 py-1 bg-red-600 hover:bg-red-700 text-white border-red-600"
                        onClick={() => handleSuspend(user._id)}
          >
            Suspend
          </Button>
          <Button 
                        variant="outline" 
            size="sm" 
                        className="text-xs px-3 py-1 bg-green-600 hover:bg-green-700 text-white border-green-600"
                        onClick={() => handleActivate(user._id)}
          >
            Activate
          </Button>
        </div>
                  </td>
                </tr>
              ))}
            </tbody>
          </table>
      </div>
      </Card>
    </div>
  );
}